"use client"

import { cn } from "@/lib/utils"
import { Card } from "@/components/ui/card"
import Image from "next/image"

interface HeroSectionProps {
  title: string
  description?: string
  image?: string
  className?: string
  children?: React.ReactNode
  overlay?: boolean
}

export function HeroSection({
  title,
  description,
  image,
  className,
  children,
  overlay = true,
}: HeroSectionProps) {
  return (
    <div className={cn("relative min-h-[400px] flex items-center justify-center", className)}>
      {image && (
        <Image
          src={image}
          alt="Hero background"
          fill
          priority
          className="object-cover"
          quality={100}
        />
      )}
      {overlay && (
        <div className="absolute inset-0 bg-black/50 z-10" />
      )}
      <Card className="relative z-20 max-w-3xl mx-4 p-8 bg-white/90 backdrop-blur-sm">
        <h1 className="text-4xl md:text-5xl font-bold text-center mb-4">
          {title}
        </h1>
        {description && (
          <p className="text-lg md:text-xl text-center text-gray-700 mb-6">
            {description}
          </p>
        )}
        {children}
      </Card>
    </div>
  )
}
